import Vue from 'vue';
import VueRouter from 'vue-router';
// import Home from "../views/Home.vue";
import HomeView from '@/views/Home/index.vue';

// 模块
import baseUnitRouters from '@/router/BaseUnit/index.js';
import echartUnitRouters from '@/router/EchartUnit/index.js';
import proRouters from '@/router/Pro/index.js';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    component: HomeView
    // redirect: '/base-unit/pageHeader'
  },
  // {
  //   path: '/about',
  //   name: 'About',
  //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // },
  baseUnitRouters,
  echartUnitRouters,
  proRouters,
  {
    path: '/*',
    name: '404',
    component: () => import('@/views/Common/404/index.vue')
    // redirect: '/base-unit/pageHeader'
  }
];

const router = new VueRouter({
  routes
});

export default router;
